import UI_CommomToastMediator from "./DefaultUI/UI_CommomToastMediator";
import UI_CommondLoadMediator from "./DefaultUI/UI_CommonLoadMediator";
import UI_CommonLockScreenMediator from "./DefaultUI/UI_CommonLockScreenMediator";
import UI_FlyPanelMediator from "./DefaultUI/UI_FlyPanelMediator";
import LTPlatform from "../Platform/LTPlatform";
import { EPlatformType } from "../Platform/EPlatformType";
export default class LTUI {
    static SetCommonBtnClickCB(handle) {
        handle.once = false;
        this._cacheBtnHandle = handle;
    }
    static TrigerBtnClick() {
        if (this._cacheBtnHandle) {
            this._cacheBtnHandle.run();
        }
    }
    static Toast(str) {
        console.log("[Toast]" + str);
        UI_CommomToastMediator.instance.Show(str);
    }
    static ShowLoading(str, isBig = false) {
        if (LTPlatform.instance.platform == EPlatformType.TT || LTPlatform.instance.platform == EPlatformType.WX) {
            return LTPlatform.instance.base.showLoading({
                title: str,
                mask: isBig
            });
        }
        if (UI_CommondLoadMediator.instance.isShow) {
            console.log("加载弹窗界面已打开");
            return;
        }
        UI_CommondLoadMediator.instance.Show([str, isBig]);
    }
    static HideLoading() {
        if (LTPlatform.instance.platform == EPlatformType.TT || LTPlatform.instance.platform == EPlatformType.WX) {
            return LTPlatform.instance.base.hideLoading();
        }
        UI_CommondLoadMediator.instance.Hide();
    }
    static BoomObjs(fromObj, boomCount = 10, flyTime = 1, circleRadius = 60) {
        return __awaiter(this, void 0, void 0, function* () {
            yield UI_FlyPanelMediator.instance.BoomObjs(fromObj, boomCount, flyTime, circleRadius);
        });
    }
    static FlyObjsTo(fromObj, toObj, flyCount = 10, flyTime = 1, circleRadius = 60) {
        return __awaiter(this, void 0, void 0, function* () {
            yield UI_FlyPanelMediator.instance.FlyObjs(fromObj, toObj, flyCount, flyTime, circleRadius);
        });
    }
    static FlyCoinsTo(fromObj, toObj, flyIcon = null, flyCount = 10, flyTime = 1, circleRadius = 60) {
        return __awaiter(this, void 0, void 0, function* () {
            yield UI_FlyPanelMediator.instance.FlyCoins(fromObj, toObj, flyIcon, flyCount, flyTime, circleRadius);
        });
    }
    static BoomCoins(fromObj, flyIcon = null, flyCount = 10, flyTime = 1, circleRadius = 60) {
        return __awaiter(this, void 0, void 0, function* () {
            yield UI_FlyPanelMediator.instance.BoomCoins(fromObj, flyIcon, flyCount, flyTime, circleRadius);
        });
    }
    static LockScreen() {
        UI_CommonLockScreenMediator.instance.Show();
    }
    static UnlockScreen() {
        UI_CommonLockScreenMediator.instance.Hide();
    }
}
