export class AndroidDevice {
    constructor(bridge) {
        this.surportVibrate = true;
        this.base = bridge;
        console.log('android device');
    }
    Vibrate(isLong) {
        console.log('调用振动', isLong);
        if (this.base) {
            if (isLong) {
                this.base.call("vibrate", 200);
            }
            else {
                this.base.call("vibrate", 50);
            }
        }
    }
}
