import GlobalUnit from "../common/GlobalUnit";
export default class CameraControl {
    InitCamera(root) {
        this._rootObj = root;
        this.camera = root.getChildAt(0);
    }
    followPlayer() {
        let old = this._rootObj.transform.position;
        let target = GlobalUnit.game.player.pos;
        old.x = target.x;
        old.y = target.y;
        this._rootObj.transform.position = old;
    }
    LateUpdate() {
        this.followPlayer();
    }
}
