import BaseUIMediator from "../FGui/BaseUIMediator";
import UI_Channel from "./UI/LTGame/UI_Channel";
import StringEx from "../../LTUtils/StringEx";
import LTUI from "../LTUI";
import CommonSaveData from "../../Commom/CommonSaveData";
export class UI_ChannelMediator extends BaseUIMediator {
    get ui() {
        return this._ui;
    }
    static get instance() {
        if (this._instance == null) {
            this._instance = new UI_ChannelMediator();
            this._instance._classDefine = UI_Channel;
        }
        return this._instance;
    }
    _OnShow() {
        super._OnShow();
        this.ui.m_btn_close.onClick(this, this.Hide);
        this.ui.m_btn_yes.onClick(this, this.bindChannel);
        this.ui.m_btn_rebind.onClick(this, this.clear);
        this.updateUI();
    }
    updateUI() {
        if (StringEx.IsNullOrEmpty(CommonSaveData.instance.channelId)) {
            this.ui.m_state.selectedIndex = 0;
        }
        else {
            this.showChannelID();
        }
    }
    clear() {
        CommonSaveData.instance.channelId = '';
        CommonSaveData.SaveToDisk();
        this.updateUI();
    }
    bindChannel() {
        let txt = this.ui.m_txt_input.text;
        if (StringEx.IsNullOrEmpty(txt)) {
            return LTUI.Toast('请重新输入');
        }
        else {
            CommonSaveData.instance.channelId = txt;
            CommonSaveData.SaveToDisk();
            LTUI.Toast('渠道绑定成功');
            this.showChannelID();
        }
    }
    showChannelID() {
        this.ui.m_state.selectedIndex = 1;
        this.ui.m_txt_show.text = `您已绑定的渠道号/抖音账号：\n${CommonSaveData.instance.channelId}`;
    }
    _OnHide() { }
}
