import DefaultRecordManager from "../../DefaultRecordManager";
export class WebRecordManager extends DefaultRecordManager {
    constructor() {
        super(...arguments);
        this.supportRecord = true;
    }
    ShareVideo(onSuccess, onCancel, onFailed) {
        if (this.supportRecord) {
            console.log("强制模拟成功");
            if (onSuccess) {
                onSuccess.run();
            }
        }
        else {
            console.log("强制模拟失败");
            if (onFailed) {
                onFailed.run();
            }
        }
    }
}
