import LTPlatform from "./LTPlatform";
export default class DefaultRecordManager {
    constructor() {
        this.supportRecord = false;
        this.isRecording = false;
        this.isPausing = false;
        this.isRecordSuccess = false;
    }
    StartRecord(onStart, onOverTime) {
        console.log("该平台" + LTPlatform.platformStr + "不支持录制视频");
    }
    StopRecord(onStop) {
        console.log("该平台" + LTPlatform.platformStr + "不支持录制视频");
    }
    Pause(onPause) {
        console.log("该平台" + LTPlatform.platformStr + "不支持录制视频");
    }
    Resume(onReume) {
        console.log("该平台" + LTPlatform.platformStr + "不支持录制视频");
    }
    RecordClip(timeRange) {
        console.log("该平台" + LTPlatform.platformStr + "不支持录制视频");
    }
    ShareVideo(onSuccess, onCancel, onFailed) {
        if (onFailed) {
            onFailed.run();
        }
    }
}
