export default class DefaultDevice {
    Vibrate(isLong) {
        console.log("调用震动", isLong);
    }
}
export class OppoDevice {
    constructor() {
        this.base = qg;
        console.log('QGDIVCE INIT', this.base);
    }
    Vibrate(isLong) {
        console.log('调用振动', isLong);
        if (this.base) {
            if (isLong) {
                this.base.vibrateLong({
                    success: (res) => {
                        console.log('振动');
                    },
                    fail: (res) => { console.log('振动失败'); },
                    complete: (res) => { }
                });
            }
            else {
                this.base.vibrateShort({
                    success: (res) => { console.log('短振动'); },
                    fail: (res) => { console.log('短振动失败'); },
                    complete: (res) => { }
                });
            }
        }
    }
}
export class VivoDevice {
    constructor() {
        this.base = qg;
        console.log('QGDIVCE INIT', this.base);
    }
    Vibrate(isLong) {
        console.log('调用振动', isLong);
        if (this.base) {
            if (isLong) {
                this.base.vibrateLong();
            }
            else {
                this.base.vibrateShort();
            }
        }
    }
}
